.card {
    display: flex;
    flex-direction: column-reverse;
    align-items: flex-end;
    width: 100%;
    height: 340px;
    position: relative;
    overflow: hidden;

    &::before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: linear-gradient(to top, rgba(0, 0, 0, 0.5), transparent 50%);
        border-radius: 10px;
        z-index: 1;
    }
    }


.card-cover {
    width: 100%;
    height: 100%;
    object-fit: cover;
    position: absolute;
    top: 0px;
    left: 0px;
    border-radius: 10px;
}


.card-title {
    color: #FFFFFF;
    font-family: Montserrat;
    font-size: 18px;
    font-weight: 700;
    line-height: 25.67px;
    padding: 10px;
    position: relative; 
    align-self: flex-start;
    text-decoration-line: none ;
    z-index: 2;
}


@media (max-width: 767.98px) {

    .card {
        max-height: 255px;
    }
 }
